import React, { useState } from 'react';
import marked from 'marked';

import logo from "./Images/logo.png";

function App() {


    const [mark, setMark] = useState('');


    return (
        <>
            <div className="container">
                <div className="logo">
                    <img src={logo} className="logo-img" alt="logo" />
                    <h2 className="brand">marktor</h2>
                </div>
                <div className="editor">

                    <textarea
                        rows="5" cols="33" placeholder="Markdown goes here..." onChange={e => setMark(e.target.value)}>

                    </textarea>

                    <div className="rendered" dangerouslySetInnerHTML={{ __html: marked(mark, { sanitize: true }) }}></div>
                </div>
            </div>
            <footer className="footer">
                built with <span role="img" aria-label="heart">❤️</span> by jana
            </footer>
        </>
    );
}

export default App;
